import React, { useState } from "react";
import "./Header.css";
import logo from "../images/sm-logo-2.svg";
//import './Header_animate'
import { Link } from "react-router-dom";
import helpdesk from "../images/helpdesk.svg";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
function Header() {
  const [reachus, setReachus] = useState({
    name: "",
    phone: "",
  });

  const { name, phone } = reachus;

  const handleReachus = (evt) => {
    const value = evt.target.value;
    setReachus({
      ...reachus,
      [evt.target.name]: value,
    });
  };

  const onSubmit = (e) => {
    e.preventDefault();
    if (name && phone) {
      axios
        .post("http://localhost:3000/api/reachus", reachus)
        .then((res) => {
          toast.success("Thank you for choosing us");
        })
        .catch((err) => console.log(err));
    } else {
      toast.error("Please provide name and phone number");
    }
    setReachus({
      name: "",
      phone: "",
    });
  };

  return (
    <div>
      <ToastContainer />

      {/* Navigation */}
      <nav className="navbar sticky">
        {/* Brand */}
        {/* <a className="navbar-brand" href="#">Spectramed</a> */}

        <Link to="/">
          <img className="logo" src={logo} alt="Logo"></img>
        </Link>
        <button
          className="btn-sm reach_us btn-success"
          data-toggle="modal"
          data-target="#exampleModal"
        >
          Reach Us
        </button>

        {/* <!-- Toggler/collapsibe Button --> */}
        {/* <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#collapsibleNavbar"
        >
          <span className="navbar-toggler-icon"></span>
        </button> */}

        {/* <!-- Navbar links --> */}
        {/* <div className="collapse navbar-collapse" id="collapsibleNavbar">
              <ul className="navbar-nav ">
              <li className="nav-item">
              <a className="nav-link" href="#">Link</a>
              
              </li>
              <li className="nav-item">
              <a className="nav-link" href="#">Link</a>
              </li>
              <li className="nav-item">
              <a className="nav-link" href="#">Link</a>
              </li>
              <li>
                 
              </li>
              </ul>
              </div> */}
      </nav>

      <div
        className="modal fade"
        id="exampleModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-body">
              <div className="row">
                <div className="col-md-8">
                  <img
                    className="help_logo img-fluid"
                    src={helpdesk}
                    alt="helpdesk"
                  ></img>
                </div>
                <div className="col-md-4">
                  <form id="reach_form">
                    <div className="form-group">
                      <label htmlFor="name">Name</label>
                      <input
                        type="text"
                        className="form-control"
                        name="name"
                        onChange={handleReachus}
                        value={name}
                        required
                      ></input>
                    </div>
                    <div className="form-group">
                      <label htmlFor="contact">Phone number</label>
                      <input
                        type="number"
                        className="form-control "
                        name="phone"
                        onChange={handleReachus}
                        value={phone}
                        required
                        max="10"
                      ></input>
                    </div>
                    <button
                      type="submit"
                      className="btn btn-info"
                      data-dismiss="modal"
                      onClick={onSubmit}
                    >
                      Submit
                    </button>
                  </form>
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header;
