import React, { useState } from "react";
import "./Banner.css";
import image from "../images/healthcare.svg";

import { Link } from "react-router-dom";

function Banner() {
  return (
    <div className="banner">
      <div className="container">
        <div className="row">
          <div className="col-md-6 text">
            <p className="title">
              One Call Booking
              <p className="subtitle">
                Call now and book any pathology or radiology test at home.{" "}
                <br /> <br />
                {/* <div className="buy_button"> */}
                <Link to="/booknow">
                  <button
                    type="button"
                    className="btn btn-primary btn-large buy_button animate__animated animate__heartBeat"
                    id="btn_text"
                    // data-toggle="modal"
                    // data-target="#registerform"
                  >
                    Book Now
                  </button>
                  {/* </div> */}
                </Link>
              </p>
            </p>
          </div>
          <div className="col-md-6">
            <img className="img-fluid doc" src={image} alt="medical"></img>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Banner;
