import React from "react";
import "./Footer.css";
import insta from "../images/icon3.png";
import fb from "../images/fb.png";
import linkedin from "../images/icon1.png";
import { Link } from "react-router-dom";

function Footer() {
  // const onclicktc = (e) => {
  //   e.preventDefault();
  //   window.location = "/termsandconditions";
  // };
  const onclickpp = (e) => {
    e.preventDefault();
    window.location = "/PrivacyPolicy";
  };

  return (
    <div className="footer">
      <div className="container-fluid">
        <div className="footer_content">
          <div className="row">
            <div className="footer_company col-md-4">
              <div className="icons">
                <h2>Follow Us</h2>
                <a
                  href="https://instagram.com/spectramedofficial?igshid=12ogzmu55k3tp"
                  target="_blank"
                >
                  <img src={insta} alt="insta"></img>
                </a>
                <a
                  href="https://www.facebook.com/Spectramed-Global-103578098193727/"
                  target="_blank"
                >
                  <img src={fb} alt="facebook"></img>
                </a>
                <a
                  href="https://www.linkedin.com/company/spectramed-global-health-management-llp"
                  target="_blank"
                >
                  <img src={linkedin} alt="linkedin"></img>
                </a>
              </div>
            </div>
            <div className="footer_policy col-md-4">
              <h2 className="text_policy">Our Policies</h2>
              <p className="font-weight-light">
                {/* <a href="#" onClick={onclicktc}>
                  Terms and conditions
                </a> */}
                {/* <Link onClick={onclicktc}> Terms and conditions</Link> */}
                <Link to="/termsandconditions">Terms and conditions</Link>
              </p>
              <p>
                {/* <a href="#" onClick={onclickpp}>
                  Privacy Policy
                </a> */}
                {/* <Link onClick={onclickpp}>Privacy Policy</Link> */}
                <Link to="/PrivacyPolicy">Privacy Policy</Link>
              </p>
            </div>
            <div className="footer_contactus col-md-4">
              <h2>Contact us</h2>
              <p>
                <a href="tel:18002669999">1800-266-9999</a>
              </p>
            </div>
            <div className="scroll-top float-right ">
              <a href="tel:18002669999">
                <i className="fas fa-phone-alt fa-2x" id="myBtn"></i>
              </a>
            </div>
          </div>
          <hr />
          <div className="row">
            <p className="copyright">
              Copyright © By Spectramed 2021. All right reserved.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;
